import { Diagnostic } from "@ionic-native/diagnostic";


import { LoginPage, ChecklistPage, InpectionTextAreaModalPage, NoInpectionTextAreaModalPage } from "./../index";
import { DashboardService } from "./../../providers/api/dashboard.service";
import { VeiculoService } from "./../../providers/api/veiculo.service";
import { User } from "./../../providers/user/user";
import { MyApp } from "./../../app/app.component";
import { Component, ElementRef, ViewChild } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ToastController,
  LoadingController,
  ModalController,
  AlertController
} from "ionic-angular";
import { LocationAccuracy } from "@ionic-native/location-accuracy";
import {
  NativeGeocoder,
  NativeGeocoderOptions
} from "@ionic-native/native-geocoder";
import { AndroidPermissions } from "@ionic-native/android-permissions";
import { GoogleMaps, GoogleMap, GoogleMapsEvent, MarkerIcon, MarkerOptions, HtmlInfoWindow, Marker, GeocoderRequest, Geocoder, BaseArrayClass, GeocoderResult } from "@ionic-native/google-maps";
import { Subscription } from "rxjs";

@IonicPage()
@Component({
  selector: "page-select-placa",
  templateUrl: "select-placa.html"
})
export class SelectPlacaPage {
  public placa = "";
  public veiculoSelected;
  public gpslocation = {};
  public subsciptionPosition: Subscription = null;
  @ViewChild("map") mapElement: ElementRef;
  public mapaGoogle: GoogleMap = null;
  public mark: Marker = null;
  public isMarked = false;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController,
    public user: User,
    public app: MyApp,
    public veiculoService: VeiculoService,
    public dashboardService: DashboardService,
    public geocoder: NativeGeocoder,
    public alertCtrl: AlertController,
    public locac: LocationAccuracy,
    private androidPermissions: AndroidPermissions,
    private diagnostic: Diagnostic
  ) {}

  ionViewDidLoad() {
    console.log("ionViewDidLoad SelectPlacaPage");
  }

  selectPlaca() {
    if(this.mark != null){
      if(this.subsciptionPosition != null){
        this.subsciptionPosition.unsubscribe();
        this.subsciptionPosition = null;
      }
      this.mapaGoogle.clear();
      this.isMarked = false;

      this.mark = null;

    }
    let loading = this.loadingCtrl.create({
      content: "Please wait..."
    });
    loading.present();
    this.veiculoService.findVeiculoByPlaca(this.placa).subscribe(
      res => {
        if (res == null) {
          let toast = this.toastCtrl.create({
            message: "Plate not found",
            duration: 3000,
            position: "top"
          });

          toast.present();
        } else {
          this.veiculoSelected = res;
          if(this.mapaGoogle == null){
            let element = this.mapElement.nativeElement;
            this.mapaGoogle = GoogleMaps.create(element);
            this.mapaGoogle.one(GoogleMapsEvent.MAP_READY).then(() => {
              // this.openInpection();

            });
          }else {
            // this.openInpection();
          }
        }
      },
      error => {
        loading.dismiss();
      },
      () => {
        loading.dismiss();
      }
    );
  }

  logout() {
    this.user.logout();
    this.app.getRootNav().setRoot(LoginPage);
  }






  redirectInpection(){
    if(this.subsciptionPosition != null){
      this.subsciptionPosition.unsubscribe();
    }

    this.navCtrl.setRoot(ChecklistPage, {
      posicao: this.mark.getTitle(),
      latitude: this.mark.getPosition().lat,
      longitude: this.mark.getPosition().lng,
      veiculo: this.veiculoSelected
    });
  }
  redirectNotInpection(){
    if(this.subsciptionPosition != null){
      this.subsciptionPosition.unsubscribe();
    }
    let profileModal = this.modalCtrl.create(NoInpectionTextAreaModalPage, {
      obs: '',
      posicao: this.mark.getTitle()
    });
    profileModal.onDidDismiss(data => {
      if (data != null && data.obs.trim().length>0) {
        let loading = this.loadingCtrl.create({
          content: "Please wait..."
        });
        loading.present();
        const inspe = {
          dh: new Date(),
          lat: this.mark.getPosition().lat,
          lng: this.mark.getPosition().lng,
          posicao: data.posicao,
          veiculoId: this.veiculoSelected.id,
          inspecaosTipo: [],
          obs: data.obs
        }

        this.veiculoService.salvarInspecao(inspe).subscribe(res => {


          let alert = this.alertCtrl.create({
            title: 'Success',
            subTitle: 'Saved successfully',
            buttons: ['OK']
          });
          alert.present();
          this.app.getRootNav().setRoot(SelectPlacaPage);
        },erro => {
          loading.dismiss();
        },() => {
          loading.dismiss();
        });
      }
    });
    profileModal.present();
  }
}



